import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS, STATUSES } from '@shared';
import { S1AndS2FormExpirationPeriod } from 'app/core/models/s1-and-s2-form-expiration-period.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-s1-and-s2-form-expiration-period',
  templateUrl: './s1-and-s2-form-expiration-period.component.html',
  styleUrls: ['./s1-and-s2-form-expiration-period.component.scss']
})
export class S1AndS2FormExpirationPeriodComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<S1AndS2FormExpirationPeriod>;
  displayedColumns: string[] = ['s1ExpNoOfYears', 's2ExpNoOfYears'];
  statusList = [...STATUSES];

  s1AndS2FormExpirationPeriod = new S1AndS2FormExpirationPeriod();
  s1AndS2FormExpirationPeriodList = [];

  s1AndS2FormExpirationPeriodForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.s1AndS2FormExpirationPeriods).subscribe((res: any) => {
      this.s1AndS2FormExpirationPeriodList = res.responseData.data;
      this.dataSource = new MatTableDataSource<S1AndS2FormExpirationPeriod>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.s1AndS2FormExpirationPeriods, this.s1AndS2FormExpirationPeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        if (!Number(this.s1AndS2FormExpirationPeriod.s1ExpNoOfYears)
          || !Number(this.s1AndS2FormExpirationPeriod.s2ExpNoOfYears)) {
          err.error.message = 'Please enter numeric values only';
        } else {
          err.error.message = 'Default expiration values have already been set. Select the entry to update the record';
        }
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  getS1AndS2FormExpirationPeriod(): void {
    this.apiService.findById(ENDPOINTS.s1AndS2FormExpirationPeriods, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.s1AndS2FormExpirationPeriod = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.s1AndS2FormExpirationPeriods, this.s1AndS2FormExpirationPeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.s1AndS2FormExpirationPeriodForm = this.formBuilder.group({
      s1ExpNoOfYears: ['', Validators.required],
      s2ExpNoOfYears: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.s1AndS2FormExpirationPeriod = new S1AndS2FormExpirationPeriod();
    this.s1AndS2FormExpirationPeriodForm.markAsUntouched();
    this.s1AndS2FormExpirationPeriodForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this S1 and S2 form expiration period?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.s1AndS2FormExpirationPeriods, this.s1AndS2FormExpirationPeriod.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('S1 and S2 form expiration period successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
